    #t_2_1.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 2.1 Identificacin de peligros
    
    .row.justify-content-center.align-items-center.mb-5
      .col-lg-8.order-lg-1.order-2(data-aos="fade-right")
        p La identificacin de peligros es un proceso fundamental en la gestin de la Seguridad y Salud en el Trabajo (SST). Este consiste en reconocer elementos, condiciones o actos presentes en el entorno laboral que puedan generar dao a la salud de los trabajadores, comprometer su integridad fsica o afectar el entorno laboral.
        .tarjeta.BG01.p-4.mb-4
          .row.justify-content-center.align-items-center
            .col-lg-2.col-4.mb-lg-0.mb-4
              img(src='@/assets/curso/tema2/img03.svg' alt="Imagen decorativa")
            .col-lg-10
              p.mb-0 En el contexto de la SST, un peligro se define como una fuente, situacin o acto con potencial de causar dao, ya sea en trminos de lesiones o enfermedades, o en afectaciones a la propiedad, el ambiente de trabajo o ambos. La correcta identificacin de estos peligros permite implementar estrategias de control eficaces.
        p.mb-0 Segn la Gua Tcnica Colombiana GTC 45 de 2012, los peligros se clasifican en las siguientes categoras:
      .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="fade-left")
        img(src='@/assets/curso/tema2/img04.jpg' alt="Imagen decorativa")

    SlyderF.mb-5(columnas="col-lg-6 col-xl-4" data-aos="zoom-in")
