        p La identificacin de peligros y la valoracin de riesgos permite establecer medidas y actividades precisas para eliminar o minimizar los riesgos en el lugar de trabajo. Para ello, es necesario aplicar una metodologa sistemtica, que garantice un anlisis integral y documentado.
        p Entre las metodologas ms utilizadas se encuentran:
        .tarjeta.BG06.p-4
          ul.lista-ul--color
            li(style="margin-bottom: 2px")
              i.fa-solid.fa-helmet-safety.TC2
              p.mb-0 #[b Mtodo INSHT (Espaa):] emplea listas de chequeo para verificar deficiencias en los lugares de trabajo.
            li(style="margin-bottom: 2px")
              i.fa-solid.fa-helmet-safety.TC2
              p.mb-0 #[b BS 8800 (Reino Unido):] gua para sistemas de gestin en SST, basada en ISO 14001 y publicada por la British Standards Institution en 1996.
            li(style="margin-bottom: 2px")
              i.fa-solid.fa-helmet-safety.TC2
              p.mb-0 #[b Mtodo FINE:] mtodo probabilstico que utiliza una frmula matemtica para calcular el grado de peligrosidad, considerando la probabilidad, las consecuencias y la exposicin.

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-8.order-lg-1.order-2(data-aos="fade-right")
        p Para efectos prcticos, se recomienda emplear como referencia la metodologa de la #[b GTC 45 de 2012], la cual proporciona directrices para la identificacin de peligros y valoracin de riesgos en las organizaciones, con la posibilidad de adaptarlas segn las caractersticas propias de cada empresa.
        p Ahora bien, en este punto es importante #[b identificar los peligros asociados a condiciones de seguridad], los cuales se refieren a condiciones peligrosas derivadas del estado de equipos, objetos, instalaciones o locaciones, que, al entrar en contacto con las personas, pueden generar lesiones fsicas o incluso la muerte, dependiendo del tiempo e intensidad del contacto.
        p.mb-0 Estos peligros incluyen:
      .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="fade-left")
        img(src='@/assets/curso/tema2/img13.jpg' alt="Imagen decorativa")

    SlyderF.mb-5(columnas="col-lg-6 col-xl-4" data-aos="zoom-in")
      .tarjeta.BGIMG06.p-4
        .row.justify-content-center.mb-4
          .col-lg-5.col-7
            img(src='@/assets/curso/tema2/img14.svg' alt="Imagen decorativa")
        p.mb-0 #[b Locativos:] relacionados con el estado de paredes, pisos, techos, sistemas de almacenamiento y superficies de trabajo. Consecuencias: cadas, golpes y otros accidentes.
      .tarjeta.BGIMG06.p-4
        .row.justify-content-center.mb-4
          .col-lg-5.col-7
            img(src='@/assets/curso/tema2/img15.svg' alt="Imagen decorativa")
        p.mb-0 #[b Mecnicos:] derivados del uso de herramientas, maquinaria, piezas mviles o materiales proyectados. Consecuencias: cortes, atrapamientos, aplastamientos, impactos, amputaciones, entre otros.
      .tarjeta.BGIMG06.p-4
        .row.justify-content-center.mb-4
          .col-lg-5.col-7
            img(src='@/assets/curso/tema2/img16.svg' alt="Imagen decorativa")
        p.mb-0 #[b Elctricos:] posibilidad de contacto directo o indirecto con corriente elctrica. Consecuencias: electrocucin, quemaduras, explosiones e incluso la muerte.
      .tarjeta.BGIMG06.p-4
        .row.justify-content-center.mb-4
          .col-lg-5.col-7
            img(src='@/assets/curso/tema2/img17.svg' alt="Imagen decorativa")
        p.mb-0 #[b Tecnolgicos:] incluyen fugas, derrames, explosiones e incendios. Consecuencias: intoxicaciones, quemaduras y prdida de vidas.
      .tarjeta.BGIMG06.p-4
        .row.justify-content-center.mb-4
          .col-lg-5.col-7
            img(src='@/assets/curso/tema2/img18.svg' alt="Imagen decorativa")
        p.mb-0 #[b Pblicos:] hacen referencia a riesgos como robos, secuestros, atentados o delitos internos. Consecuencias: lesiones fsicas, daos psicolgicos e incluso fallecimientos.
      .tarjeta.BGIMG06.p-4
        .row.justify-content-center.mb-4
          .col-lg-5.col-7
            img(src='@/assets/curso/tema2/img19.svg' alt="Imagen decorativa")
