    #t_2_2.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 2.2 Riesgos y medidas de intervencin
    
    .row.justify-content-center.mb-5
      .col-lg-6.px-0.order-lg-1.order-2(data-aos="fade-right")
        .BG08.h-100.p-4
          .row.mb-4
            .col-lg-2.col-4
              img.w-75(src='@/assets/curso/tema2/img23.svg' alt="Imagen decorativa")
          p.mb-0 El riesgo en el contexto de la seguridad y salud en el trabajo se refiere a la probabilidad de que un peligro cause dao, lesin o enfermedad en el entorno laboral. Este riesgo est determinado por la exposicin al peligro y la gravedad de las posibles consecuencias. Mientras que el peligro es la fuente que puede causar dao, el riesgo es la probabilidad de que ese dao ocurra. La gestin de riesgos implica evaluar y reducir la probabilidad de que se materialicen los peligros, as como mitigar la severidad de sus efectos.
      .col-lg-6.px-0.order-lg-2.order-1(data-aos="fade-left")
        img(src='@/assets/curso/tema2/img24.jpg' alt="Imagen decorativa")

    p.mb-5 Los riesgos se clasifican segn las fuentes de peligros presentes en el entorno laboral. Algunos de los principales tipos de riesgos incluyen:

    .row.justify-content-center.mb-4
      .col-lg-3.col-6.mb-4(data-aos="fade-right")
        .tarjeta--boton.BGIMG07.h-100.p-4
            .row.justify-content-center.mb-4
              .col-6
                img(src='@/assets/curso/tema2/img25.svg', alt="Imagen decorativa")
            h4.text-center Riesgos fsicos
            p.mb-0 Son aquellos derivados de factores ambientales, como ruido, vibraciones, temperaturas extremas, radiaciones, etc.
      .col-lg-3.col-6.mb-4(data-aos="flpi-right")
        .tarjeta--boton.BGIMG07.h-100.p-4
            .row.justify-content-center.mb-4
              .col-6
                img(src='@/assets/curso/tema2/img26.svg', alt="Imagen decorativa")
            h4.text-center Riesgos qumicos
            p.mb-0 Provienen de la exposicin a sustancias qumicas peligrosas, como gases, vapores, lquidos, polvos, entre otros.
      .col-lg-3.col-6.mb-4(data-aos="flpi-left")
        .tarjeta--boton.BGIMG07.h-100.p-4
            .row.justify-content-center.mb-4
              .col-6
                img(src='@/assets/curso/tema2/img27.svg', alt="Imagen decorativa")
            h4.text-center Riesgos biolgicos
            p.mb-0 Involucran la exposicin a virus, bacterias, hongos y otros microorganismos que pueden causar enfermedades.
      .col-lg-3.col-6.mb-4(data-aos="fade-left")
        .tarjeta--boton.BGIMG07.h-100.p-4
            .row.justify-content-center.mb-4
              .col-6
                img(src='@/assets/curso/tema2/img28.svg', alt="Imagen decorativa")
            h4.text-center Riesgos ergonmicos
            p.mb-0 Estn relacionados con posturas inadecuadas, movimientos repetitivos, esfuerzo fsico excesivo, y otros factores que afectan la salud musculoesqueltica del trabajador.
      .col-lg-3.col-6.mb-4(data-aos="fade-right")
        .tarjeta--boton.BGIMG07.h-100.p-4
            .row.justify-content-center.mb-4
              .col-6
                img(src='@/assets/curso/tema2/img29.svg', alt="Imagen decorativa")
            h4.text-center Riesgos psicosociales
            p.mb-0 Se derivan de factores como el estrs, la carga de trabajo, el acoso laboral, y las relaciones laborales inadecuadas.
      .col-lg-3.col-6.mb-4(data-aos="flip-right")
        .tarjeta--boton.BGIMG07.h-100.p-4
            .row.justify-content-center.mb-4
              .col-6
                img(src='@/assets/curso/tema2/img30.svg', alt="Imagen decorativa")
            h4.text-center Riesgos mecnicos o de seguridad
            p.mb-0 Relacionados con la exposicin a maquinarias defectuosas, herramientas mal mantenidas, o condiciones inseguras en el lugar de trabajo.
      .col-lg-3.col-6.mb-4(data-aos="flip-left")
        .tarjeta--boton.BGIMG07.h-100.p-4
            .row.justify-content-center.mb-4
              .col-6
                img(src='@/assets/curso/tema2/img31.svg', alt="Imagen decorativa")
            h4.text-center Riesgos tecnolgicos
            p.mb-0 Estos riesgos surgen por fallos en equipos o procesos tecnolgicos, como fugas, incendios, o explosiones.
      .col-lg-3.col-6.mb-4(data-aos="fade-left")
        .tarjeta--boton.BGIMG07.h-100.p-4
            .row.justify-content-center.mb-4
              .col-6
                img(src='@/assets/curso/tema2/img32.svg', alt="Imagen decorativa")
            h4.text-center Riesgos naturales
            p.mb-0 Se derivan de fenmenos naturales como terremotos, tormentas, inundaciones, etc., que pueden afectar la seguridad de los trabajadores y las instalaciones.

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-1.col-2.px-0.mb-lg-0.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/temas/img01.svg' alt="Imagen decorativa")
      .col-lg-11(data-aos="fade-left")
        p.mb-0 Una vez que se han identificado los peligros y evaluado los riesgos, es crucial establecer medidas de intervencin para eliminarlos o controlarlos. Estas acciones deben ser adoptadas segn la jerarqua de control de riesgos, priorizando las medidas ms efectivas.
    
    .row.justify-content-center.mb-4
      .col-lg-10
        p Las medidas de intervencin se dividen en las siguientes categoras:
        AcordionA(tipo="a" clase-tarjeta="tarjeta BG04" data-aos="zoom-in")
          .row.justify-content-center(titulo="Eliminacin del peligro")
            .col-lg-6.order-lg-1.order-2
              p.mb-0 Consiste en suprimir completamente la fuente del riesgo. Es la medida ms eficaz, aunque no siempre posible. Por ejemplo, eliminar el uso de una sustancia txica reemplazndola por otra segura.
            .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
              img(src='@/assets/curso/tema2/img33.jpg' alt="Imagen decorativa")
          .row.justify-content-center(titulo="Sustitucin")
            .col-lg-6.order-lg-1.order-2
              p.mb-0 Se refiere al reemplazo de materiales, procesos o equipos peligrosos por otros que presenten menor riesgo. Un ejemplo es cambiar una mquina ruidosa por una de menor emisin sonora.
            .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
              img(src='@/assets/curso/tema2/img34.jpg' alt="Imagen decorativa")
          .row.justify-content-center(titulo="Controles de ingeniera")
            .col-lg-6.order-lg-1.order-2
              p.mb-0 Son modificaciones fsicas al ambiente laboral, como instalar sistemas de ventilacin, barreras de proteccin, encierros de maquinaria o aislamiento de fuentes peligrosas.
            .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
              img(src='@/assets/curso/tema2/img35.jpg' alt="Imagen decorativa")
          .row.justify-content-center(titulo="Controles administrativos")
            .col-lg-6.order-lg-1.order-2
              p.mb-0 Implican cambios en la organizacin del trabajo, como establecer pausas activas, rotacin de personal, capacitacin, sealizacin y procedimientos seguros.
            .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
              img(src='@/assets/curso/tema2/img36.jpg' alt="Imagen decorativa")
          .row.justify-content-center(titulo="Elementos de proteccin personal (EPP)")
            .col-lg-6.order-lg-1.order-2
              p.mb-0 Se usan cuando no es posible eliminar el peligro por otros medios. Incluyen cascos, guantes, gafas de seguridad, tapabocas, protectores auditivos, entre otros. Deben ser seleccionados adecuadamente y utilizados correctamente.
            .col-lg-6.col-10.order-lg-2.order-1.mb-lg-0.mb-4
              img(src='@/assets/curso/tema2/img37.jpg' alt="Imagen decorativa")

    p.mb-5 La efectividad de estas medidas depende de su correcta planificacin, implementacin, seguimiento y mejora continua, enmarcada dentro del SG-SST y en cumplimiento de la normativa nacional vigente.

    .tarjeta.BG01.p-4.mb-4(data-aos="fade-right")
      .row.justify-content-around.align-items-center
        .col-3.col-sm-2.col-lg-1
          img(src='@/assets/curso/tema1/img51.svg' alt="Imagen decorativa")
        .col
          .row.justify-content-between.align-items-center
            .col.mb-sm-0.mb-4
              h4.TC3 Gua para la identificacin de peligros, valoracin de riesgos y determinacin de controles
              p.mb-0 Para mayor informacin sobre la identificacin, evaluacin de riesgos y medidas de intervencin, se recomienda consultar la GTC 45 de 2012, disponible en el siguiente enlace:
            .col-sm-auto
              a.boton.color-acento-botones(href="https://www.minsalud.gov.co/Ministerio/Institucional/Procesos%20y%20procedimientos/GTHG01.pdf" target="_blank")
                span Descargar
                i.fas.fa-file-download

    .BGIMG01.px-md-5.px-4.mb-5
      separador
      #t_2_3.titulo-segundo.color-acento-contenido(data-aos="fade-right")
        h2 2.3 Exposicin
      
      p La exposicin se refiere a la situacin en la cual una persona entra en contacto con uno o varios peligros presentes en su entorno laboral, lo que puede generar un riesgo para su salud o seguridad. Comprender los niveles de exposicin permite evaluar con mayor precisin la probabilidad de ocurrencia de un evento no deseado y, por tanto, tomar decisiones adecuadas para su control.
      p.mb-5 Segn la Gua Tcnica Colombiana GTC 45 (2012), la exposicin puede clasificarse en:

      .row.justify-content-center.mb-5
        .col-lg-3.col-sm-6.col-10.mb-lg-0.mb-4(data-aos="fade-right")
          .tarjeta.color-primario.h-100
            figure
              img(src='@/assets/curso/tema2/img38.png' alt="Imagen decorativa")
            .p-4
              p.mb-0 #[b 1. Continua:] la persona se encuentra expuesta sin interrupciones o de forma prolongada durante la jornada laboral. Este tipo de exposicin representa un alto nivel de riesgo por su duracin sostenida.
        .col-lg-3.col-sm-6.col-10.mb-lg-0.mb-4(data-aos="flpi-right")
          .tarjeta.color-secundario.h-100
            figure
              img(src='@/assets/curso/tema2/img39.png' alt="Imagen decorativa")
            .p-4
              p.mb-0 #[b 2. Frecuente:] la exposicin ocurre varias veces durante la jornada, pero en periodos cortos. Aunque menos intensa que la continua, sigue siendo significativa por su repetitividad.
        .col-lg-3.col-sm-6.col-10.mb-lg-0.mb-4(data-aos="flpi-left")
          .tarjeta.color-primario.h-100
            figure
              img(src='@/assets/curso/tema2/img40.png' alt="Imagen decorativa")
            .p-4
              p.mb-0 #[b 3. Ocasional:] la persona entra en contacto con el peligro solo una o pocas veces en la jornada laboral, y por un tiempo breve. El riesgo es menor, pero no debe ignorarse.
        .col-lg-3.col-sm-6.col-10.mb-lg-0.mb-4(data-aos="fade-left")
          .tarjeta.color-secundario.h-100
            figure
              img(src='@/assets/curso/tema2/img41.png' alt="Imagen decorativa")
            .p-4
              p.mb-0 #[b 4. Espordica:] la exposicin se da de forma muy eventual o accidental, con baja frecuencia. Aun as, puede implicar riesgos importantes dependiendo de la naturaleza del peligro.

    p Determinar el tipo de exposicin es esencial para evaluar el nivel de riesgo y definir la urgencia y tipo de medida de intervencin que debe aplicarse. Esta informacin debe registrarse y actualizarse como parte del diagnstico del Sistema de Gestin de la Seguridad y Salud en el Trabajo (SG-SST).

    separador
    #t_2_4.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 2.4 Actos y condiciones subestndar
    
    .row.justify-content-center.mb-5
      .col-lg-2.col-4.mb-lg-0.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/tema2/img42.svg' alt="Imagen decorativa")
      .col-lg-10(data-aos="fade-left")
        p.mb-0 En el mbito de la seguridad y salud en el trabajo, es fundamental identificar las causas que pueden generar accidentes o enfermedades laborales. Estas causas se clasifican en dos grandes categoras: los actos subestndar y las condiciones subestndar. Comprender estas diferencias permite a las organizaciones implementar medidas preventivas eficaces y reducir la ocurrencia de incidentes que afecten la integridad fsica, mental o social de los trabajadores. Tanto los actos como las condiciones subestndar son indicadores de fallas en la cultura preventiva de la empresa y deben ser intervenidos oportunamente dentro del Sistema de Gestin de la Seguridad y Salud en el Trabajo (SG-SST).

    .row.justify-content-center.align-items-center.mb-5
      .col-lg-8.order-lg-1.order-2(data-aos="fade-right")
        .BG06.p-4
          p #[b a) Acto subestndar:] es toda accin u omisin cometida por un trabajador que incrementa la probabilidad de ocurrencia de un accidente o enfermedad laboral. Generalmente, los actos subestndar son voluntarios o producto del desconocimiento, y reflejan comportamientos inseguros dentro del entorno de trabajo. Ejemplos de actos subestndar (NTC 3701):
          ul.lista-ul--color
            li(style="margin-bottom: 2px")
              i.fa-solid.fa-helmet-safety.TC2
              | No comunicar un peligro identificado.
            li(style="margin-bottom: 2px")
              i.fa-solid.fa-helmet-safety.TC2
              | Distraer la atencin de un compaero durante una labor crtica.
            li(style="margin-bottom: 2px")
              i.fa-solid.fa-helmet-safety.TC2
              | Operar maquinaria sin la debida autorizacin.
            li(style="margin-bottom: 2px")
              i.fa-solid.fa-helmet-safety.TC2
              | Utilizar herramientas defectuosas o en mal estado.
          p #[b b) Condicin subestndar:] es toda situacin anmala en el entorno laboral que representa un riesgo potencial para la seguridad o salud del trabajador. Se relaciona principalmente con fallas en la infraestructura, maquinaria, equipos o ambiente de trabajo. Ejemplos de condiciones subestndar (NTC 3701):
          ul.lista-ul--color
            li(style="margin-bottom: 2px")
              i.fa-solid.fa-helmet-safety.TC2
              | Mquinas sin mantenimiento preventivo o con sistemas de seguridad ausentes.
            li(style="margin-bottom: 2px")
              i.fa-solid.fa-helmet-safety.TC2
              | Instalaciones elctricas defectuosas.
            li(style="margin-bottom: 2px")
              i.fa-solid.fa-helmet-safety.TC2
              | Iluminacin inadecuada en reas operativas.
            li(style="margin-bottom: 2px")
              i.fa-solid.fa-helmet-safety.TC2
              | Almacenamiento inadecuado de sustancias qumicas.
      .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="fade-left")
        img(src='@/assets/curso/tema2/img43.jpg' alt="Imagen decorativa")

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-1.col-2.px-0.mb-lg-0.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/temas/img01.svg' alt="Imagen decorativa")
      .col-lg-11(data-aos="fade-left")
        p.mb-0 Estas situaciones deben ser identificadas a travs de inspecciones peridicas, reportes de los trabajadores y diagnsticos tcnicos, para implementar acciones correctivas que minimicen los riesgos.
      
    separador
    #t_2_5.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 2.5 Enfermedad y accidente laboral
    
    p.mb-5 Uno de los objetivos principales del Sistema de Gestin de la Seguridad y Salud en el Trabajo (SG-SST) es prevenir la ocurrencia de enfermedades y accidentes laborales. Para ello, es necesario comprender las diferencias entre ambos conceptos, conocer su definicin legal y entender las implicaciones que tienen para los trabajadores y las organizaciones. La correcta identificacin y reporte de estos eventos permite a las empresas tomar decisiones acertadas en materia de prevencin, rehabilitacin y compensacin, conforme a lo establecido en la legislacin colombiana.

    .mb-4.d-flex(data-aos="fade-right")
      .d-flex.flex-wrap.align-items-center.pe-4(style="background-color: #B0EBD5; border-radius: 5px;")
        img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
        h3.col.ps-3.mb-0 Enfermedad laboral
      
    p.mb-5 De acuerdo con el artculo 4 del Decreto 1295 de 1994 (modificado por el Decreto 1477 de 2014), una enfermedad laboral es aquella contrada como resultado de la exposicin a factores de riesgo inherentes a la actividad laboral o del medio en el que el trabajador se ve obligado a laborar. El Decreto 1477 de 2014 establece la tabla de enfermedades laborales reconocidas en Colombia, clasificadas segn los factores de riesgo fsicos, qumicos, biolgicos, psicosociales, entre otros. La inclusin en esta tabla es fundamental para que una enfermedad sea reconocida y cubierta por el sistema de riesgos laborales. A continuacin, se presentan algunos ejemplos:

    .row.justify-content-center.mb-5
      .col-lg-4.col-sm-6.col-10.mb-lg-0.mb-4(data-aos="fade-right")
        .tarjeta.color-primario.h-100.p-4
          .row.mb-4
            .col-lg-3.col-5
              img(src='@/assets/curso/tema2/img44.svg' alt="Imagen decorativa")
          p.mb-0 Prdida auditiva inducida por ruido continuo en ambientes industriales.
      .col-lg-4.col-sm-6.col-10.mb-lg-0.mb-4(data-aos="flip-up")
        .tarjeta.color-secundario.h-100.p-4
          .row.mb-4
            .col-lg-3.col-5
              img(src='@/assets/curso/tema2/img45.svg' alt="Imagen decorativa")
          p.mb-0 Dermatitis causada por contacto frecuente con sustancias qumicas sin proteccin adecuada.
      .col-lg-4.col-sm-6.col-10.mb-lg-0.mb-4(data-aos="fade-left")
        .tarjeta.color-acento-contenido.h-100.p-4
          .row.mb-4
            .col-lg-3.col-5
              img(src='@/assets/curso/tema2/img46.svg' alt="Imagen decorativa")
          p.mb-0 Trastornos musculoesquelticos por movimientos repetitivos o posturas prolongadas.

    .mb-4.d-flex(data-aos="fade-right")
      .d-flex.flex-wrap.align-items-center.pe-4(style="background-color: #B0EBD5; border-radius: 5px;")
        img(src='@/assets/curso/temas/img02.svg' style="max-width: 32px; margin-left: -16px;")
        h3.col.ps-3.mb-0 Accidente de trabajo
    
    p.mb-5 Segn el artculo 3 del Decreto 1295 de 1994, un accidente de trabajo es todo suceso repentino que sobrevenga por causa o con ocasin del trabajo, y que produzca en el trabajador una lesin orgnica, una perturbacin funcional, una invalidez o la muerte. Tambin se considera accidente de trabajo aquel que ocurre durante el traslado directo del trabajador entre su residencia y el lugar de trabajo. A continuacin, se presentan algunos ejemplos:

    .row.justify-content-center.mb-5
      .col-lg-4.col-sm-6.col-10.mb-lg-0.mb-4(data-aos="fade-right")
        .tarjeta.color-primario.h-100.p-4
          .row.mb-4
            .col-lg-3.col-5
              img(src='@/assets/curso/tema2/img47.svg' alt="Imagen decorativa")
          p.mb-0 Un trabajador que sufre una cada al resbalar en una zona hmeda sin sealizacin.
      .col-lg-4.col-sm-6.col-10.mb-lg-0.mb-4(data-aos="flip-up")
        .tarjeta.color-secundario.h-100.p-4
          .row.mb-4
            .col-lg-3.col-5
              img(src='@/assets/curso/tema2/img48.svg' alt="Imagen decorativa")
          p.mb-0 Una lesin muscular ocasionada al levantar una carga pesada sin ayuda tcnica.
      .col-lg-4.col-sm-6.col-10.mb-lg-0.mb-4(data-aos="fade-left")
        .tarjeta.color-acento-contenido.h-100.p-4
          .row.mb-4
            .col-lg-3.col-5
              img(src='@/assets/curso/tema2/img49.svg' alt="Imagen decorativa")
          p.mb-0 Un accidente vial ocurrido mientras el trabajador se desplaza al trabajo en una ruta directa.

    .row.justify-content-center.mb-5
      .col-lg-4.col-8.px-0.mb-lg-0.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/tema2/img50.jpg' alt="Imagen decorativa")
      .col-lg-8.px-0(data-aos="fade-left")
        .BG07.h-100.p-4
          p Ambos eventos deben ser reportados oportunamente al empleador, al Sistema General de Riesgos Laborales y a la ARL correspondiente, para garantizar una atencin adecuada, la investigacin del suceso y la adopcin de medidas preventivas y correctivas.
          p.mb-0 La Ley 1562 de 2012, por su parte, refuerza el enfoque preventivo del SG-SST y ampla el concepto de salud ocupacional, promoviendo entornos de trabajo seguros y saludables, e incluyendo el bienestar fsico, mental y social del trabajador.

    .tarjeta.BG01.p-4.mb-4(data-aos="fade-right")
      .row.justify-content-around.align-items-center
        .col-3.col-sm-2.col-lg-1
          img(src='@/assets/curso/tema1/img51.svg' alt="Imagen decorativa")
        .col
          .row.justify-content-between.align-items-center
            .col.mb-sm-0.mb-4
              h4.TC3 Sistema de riesgos laborales
              p.mb-0 Para revisar este y otros conceptos en la normativa vigente consulte la Ley 1562 de 2012.
            .col-sm-auto
              a.boton.color-acento-botones(href="https://www.minsalud.gov.co/sites/rid/Lists/BibliotecaDigital/RIDE/DE/DIJ/Ley-1562-de-2012.pdf" target="_blank")
                span Descargar
                i.fas.fa-file-download

    p.mb-5 Para facilitar la comprensin de las diferencias y caractersticas entre enfermedad laboral y accidente de trabajo, a continuacin, se presenta un cuadro comparativo que resume sus principales aspectos:

    .row.justify-content-center.mb-5
      .col-lg-10
        .titulo-sexto.color-acento-contenido(data-aos="fade-right")
          h5 Tabla 2.
          i Cuadro comparativo entre enfermedad laboral vs. accidente de trabajo
        .tabla-a.color-acento-contenido
          table(data-aos="zoom-in"  alt="Texto alternativo tabla")
            thead(style="border-color: transparent")
              tr(data-aos="fade-right")
                th(style="background-color: #3ACC97; width: 20%; font-size: 18px;") Criterio
                th(style="background-color: #3ACC97; width: 40%; font-size: 18px;") Enfermedad laboral
                th(style="background-color: #3ACC97; width: 40%; font-size: 18px;") Accidente de trabajo
            tbody
              tr(data-aos="fade-right")
                th(style="background-color: #F6F6F6;") Origen
                td(style="background-color: #F6F6F6;") Exposicin prolongada a factores de riesgo inherentes al trabajo.
                td(style="background-color: #F6F6F6;") Evento repentino ocurrido por causa o con ocasin del trabajo.
              tr(data-aos="fade-right")
                th(style="background-color: #FFF;") Tiempo de manifestacin
                td(style="background-color: #FFF;") Progresivo, puede tardar das, meses o aos en aparecer.
                td(style="background-color: #FFF;") Inmediato o en corto plazo tras el evento.
              tr(data-aos="fade-right")
                th(style="background-color: #F6F6F6;") Ejemplos
                td(style="background-color: #F6F6F6;") Hipoacusia por ruido, lesiones msculo-esquelticas, enfermedades respiratorias.
                td(style="background-color: #F6F6F6;") Cadas, golpes, cortaduras, electrocuciones.
              tr(data-aos="fade-right")
                th(style="background-color: #FFF;") Reconocimiento legal
                td(style="background-color: #FFF;") Requiere estar incluida en la tabla de enfermedades del Decreto 1477 de 2014.
                td(style="background-color: #FFF;") Se reconoce si ocurre en relacin directa con el trabajo o en el trayecto laboral.
              tr(data-aos="fade-right")
                th(style="background-color: #F6F6F6;") Implicaciones
                td(style="background-color: #F6F6F6;") Requiere diagnstico mdico, reporte y validacin por la ARL para reconocimiento.
                td(style="background-color: #F6F6F6;") Debe reportarse inmediatamente; puede requerir investigacin del accidente.
              tr(data-aos="fade-right")
                th(style="background-color: #FFF;") Prevencin
                td(style="background-color: #FFF;") Mediante controles a los factores de riesgo en el ambiente laboral.
                td(style="background-color: #FFF;") Mediante sealizacin, capacitacin, controles tcnicos y administrativos.
    
    separador
    #t_2_6.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 2.6 Condiciones de salud
    
    .row.justify-content-center.align-items-center.mb-5
      .col-lg-8.order-lg-1.order-2(data-aos="fade-right")
        p Las condiciones de salud hacen referencia al conjunto de factores fisiolgicos, psicolgicos, sociales y culturales que afectan el bienestar de los trabajadores y que permiten establecer un perfil general de la poblacin laboral. Estos factores pueden ser identificados tanto a travs de mediciones objetivas como por medio del autorreporte de los trabajadores.
        .tarjeta.BG01.p-4.mb-4
          .row.justify-content-center.align-items-center
            .col-lg-2.col-4.mb-lg-0.mb-4
              img(src='@/assets/curso/tema2/img51.svg' alt="Imagen decorativa")
            .col-lg-10
              p.mb-0  Segn la Ley 1562 de 2012, las condiciones de salud incluyen elementos como antecedentes mdicos, presencia de enfermedades crnicas, condiciones mentales, hbitos de vida, entorno familiar y social, entre otros. Su anlisis permite identificar tendencias de morbilidad y factores de riesgo prevalentes en un grupo de trabajo.
        p Evaluar las condiciones de salud es fundamental para:
        .tarjeta.BG06.p-4
          ul.lista-ul--color
            li(style="margin-bottom: 2px")
              i.fa-solid.fa-helmet-safety.TC2
              | Disear e implementar programas de promocin de la salud y prevencin de enfermedades.
            li(style="margin-bottom: 2px")
              i.fa-solid.fa-helmet-safety.TC2
              | Detectar riesgos especficos que puedan agravarse en ciertos puestos de trabajo.
            li(style="margin-bottom: 2px")
              i.fa-solid.fa-helmet-safety.TC2
              | Tomar decisiones informadas sobre adaptaciones laborales o reintegros.
            li(style="margin-bottom: 2px")
              i.fa-solid.fa-helmet-safety.TC2
              | Garantizar ambientes laborales saludables y equitativos.
      .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="fade-left")
        img(src='@/assets/curso/tema2/img52.jpg' alt="Imagen decorativa")

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-1.col-2.px-0.mb-lg-0.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/temas/img01.svg' alt="Imagen decorativa")
      .col-lg-11(data-aos="fade-left")
        p.mb-0 La informacin recopilada debe ser tratada bajo criterios ticos de confidencialidad, y su uso debe estar orientado a la mejora continua del Sistema de Gestin de la Seguridad y Salud en el Trabajo (SG-SST).
    
    separador
    #t_2_7.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 2.7 Estructuras organizacionales del SG-SST
    
    p.mb-5 El Sistema de Gestin de la Seguridad y Salud en el Trabajo (SG-SST) requiere una estructura organizacional definida que garantice la planificacin, ejecucin, evaluacin y mejora continua de las acciones preventivas dentro de una empresa. Esta estructura est compuesta por roles, responsabilidades y funciones especficas asignadas a diferentes actores que, en conjunto, velan por la proteccin y bienestar de los trabajadores. Su diseo depende del tamao de la empresa, el nivel de riesgo y la normatividad vigente en Colombia.

    .BGIMG01.px-md-5.px-4.mb-5
      .row.justify-content-center.mb-4(data-aos="fade-right")
        .col-lg-3.col-6.px-0.mb-lg-0.mb-4
          img(src='@/assets/curso/tema2/img53.png' alt="Imagen decorativa")
        .col-lg-7.px-0
          .BG06.h-100.p-4
            .h4 Comit Paritario de Seguridad y Salud en el Trabajo (COPASST)
            p.mb-0 Es un organismo de promocin y vigilancia encargado de velar por el cumplimiento de las normas y programas de seguridad y salud en el trabajo. Se conforma de manera paritaria entre representantes del empleador y de los trabajadores. Es obligatorio en empresas con ms de 10 trabajadores.
      .row.justify-content-center.mb-4(data-aos="fade-right")
        .col-lg-3.col-6.px-0.mb-lg-0.mb-4
          img(src='@/assets/curso/tema2/img54.png' alt="Imagen decorativa")
        .col-lg-7.px-0
          .BG06.h-100.p-4
            .h4 Viga de Seguridad y Salud en el Trabajo
            p.mb-0 Aplica para empresas con menos de 10 trabajadores, donde no es obligatorio conformar un COPASST. El viga cumple funciones similares en cuanto a la promocin de la seguridad y la identificacin de riesgos.
      .row.justify-content-center.mb-4(data-aos="fade-right")
        .col-lg-3.col-6.px-0.mb-lg-0.mb-4
          img(src='@/assets/curso/tema2/img55.png' alt="Imagen decorativa")
        .col-lg-7.px-0
          .BG06.h-100.p-4
            .h4 Responsable del SG-SST
            p.mb-0 Es la persona designada por la empresa para coordinar la implementacin, mantenimiento y mejora del sistema. Debe contar con formacin y experiencia en el tema, y su perfil est regulado por el Decreto 1072 de 2015 y la Resolucin 0312 de 2019.
      .row.justify-content-center.mb-4(data-aos="fade-right")
        .col-lg-3.col-6.px-0.mb-lg-0.mb-4
          img(src='@/assets/curso/tema2/img56.png' alt="Imagen decorativa")
        .col-lg-7.px-0
          .BG06.h-100.p-4
            .h4 Alta direccin
            p.mb-0 Tiene la responsabilidad de liderar, apoyar y proporcionar los recursos necesarios para el cumplimiento del SG-SST. Su compromiso se refleja en la definicin de polticas, asignacin de roles, y toma de decisiones informadas con base en los resultados del sistema.

    .BG01.mb-5(data-aos="fade-right")
      .row.justify-content-center
        .col-lg-7.order-lg-1.order-2.px-0
          .p-4
            .row.mb-4
              .col-lg-2.col-4
                img(src='@/assets/curso/tema2/img57.svg' alt="Imagen decorativa")
            p El COPASST debe estar conformado por un nmero igual de representantes del empleador y de los trabajadores. Su propsito es hacer seguimiento a las condiciones de seguridad y salud en el trabajo, promover ambientes laborales saludables y canalizar las inquietudes de los trabajadores hacia la administracin.
            p.mb-0 Segn la normativa vigente, todas las empresas pblicas o privadas con diez (10) o ms trabajadores estn obligadas a conformar este comit. En empresas con menos de diez trabajadores, se debe designar un viga de seguridad y salud en el trabajo.
        .col-lg-5.col-10.order-lg-2.order-1.px-0.mb-lg-0.mb-4
          img(src='@/assets/curso/tema2/img58.png' alt="Imagen decorativa")
        
    .row.justify-content-center.mb-5
      .col-lg-10
        .titulo-sexto.color-acento-contenido(data-aos="fade-right")
          h5 Tabla 3.
          i Conformacin del COPASST segn el tamao de la empresa
