    p.mb-5 Funciones principales del COPASST:

    .row.justify-content-center.mb-5
      .col-lg-3.col-sm-6.col-10.mb-4(data-aos="fade-right")
        .tarjeta.BGIMG04.h-100.p-4
          .row.mb-4
            .col-lg-3.col-5
              img(src='@/assets/curso/temas/img03.svg' alt="Imagen decorativa")
          p.mb-0 Proponer medidas y actividades para mantener y mejorar las condiciones de salud y seguridad en el trabajo.
      .col-lg-3.col-sm-6.col-10.mb-4(data-aos="flip-right")
        .tarjeta.BGIMG04.h-100.p-4
          .row.mb-4
            .col-lg-3.col-5
              img(src='@/assets/curso/temas/img03.svg' alt="Imagen decorativa")
          p.mb-0 Participar en la capacitacin de trabajadores, supervisores y directivos en temas de salud ocupacional.
      .col-lg-3.col-sm-6.col-10.mb-4(data-aos="flip-left")
        .tarjeta.BGIMG04.h-100.p-4
          .row.mb-4
            .col-lg-3.col-5
              img(src='@/assets/curso/temas/img03.svg' alt="Imagen decorativa")
          p.mb-0 Vigilar el cumplimiento de las actividades en medicina, higiene y seguridad industrial, promoviendo la difusin de las normas y reglamentos.
      .col-lg-3.col-sm-6.col-10.mb-4(data-aos="fade-left")
        .tarjeta.BGIMG04.h-100.p-4
          .row.mb-4
            .col-lg-3.col-5
              img(src='@/assets/curso/temas/img03.svg' alt="Imagen decorativa")
          p.mb-0 Colaborar en el anlisis de las causas de accidentes y enfermedades laborales, proponiendo medidas correctivas pertinentes.

    p.mb-5 Este comit est regulado por la Resolucin 2013 de 1986 y el Decreto 1072 de 2015. Para ms informacin, se pueden consultar las normas en los siguientes enlaces:

    .row.justify-content-center.mb-5
      .col-lg-5.mb-lg-0.mb-4(data-aos="fade-right")
        a.anexo.h-100.mb-4(href="https://www.alcaldiabogota.gov.co/sisjur/normas/Norma1.jsp?i=5411" target="_blank" data-aos="fade-right")
