            p.mb-0 Resolucin 2013 de 1986 - Por la cual se reglamenta la organizacin y funcionamiento de los Comits de Medicina, Higiene y Seguridad Industrial en los lugares de trabajo.
      .col-lg-5.mb-lg-0.mb-4(data-aos="fade-left")
        a.anexo.h-100.mb-4(href="https://www.funcionpublica.gov.co/eva/gestornormativo/norma.php?i=72173" target="_blank" data-aos="fade-right")
          .anexo__icono
            img(src="@/assets/template/icono-link.svg")
          .anexo__texto
            p.mb-0 Decreto 1072 de 2015 - Decreto nico Reglamentario del Sector Trabajo.
    
    separador
    #t_2_8.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 2.8 Programas de higiene y seguridad en el trabajo
    
    .row.justify-content-center.align-items-center.mb-5
      .col-lg-8.order-lg-1.order-2(data-aos="fade-right")
        p Los programas de higiene y seguridad en el trabajo son componentes esenciales dentro del Sistema de Gestin de la Seguridad y Salud en el Trabajo (SG-SST), diseados para gestionar los riesgos identificados en cada actividad econmica y proceso productivo. Su planificacin se basa en los resultados de la identificacin de peligros y la valoracin de riesgos, y su implementacin est liderada por el responsable del SG-SST, con el apoyo del Comit Paritario de Seguridad y Salud en el Trabajo (COPASST) y la alta direccin.
        .tarjeta.BG04.p-4.mb-4
          .row.justify-content-center
            .col-lg-1.col-2
              img(src='@/assets/curso/tema2/img59.svg' alt="Imagen decorativa")
            .col
              p.mb-0 Estos programas consisten en un conjunto de actividades sistemticas, desarrolladas en un periodo de tiempo determinado, orientadas a prevenir la ocurrencia de accidentes laborales y enfermedades de origen ocupacional, mejorando as las condiciones de trabajo y promoviendo una cultura de prevencin.
        p.mb-0 Programas fundamentales en el SG-SST:
      .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="fade-left")
        img(src='@/assets/curso/tema2/img60.png' alt="Imagen decorativa")

    .BGIMG01.px-md-5.px-4.mb-5
      .row.justify-content-center.align-items-center
        .col-lg-3.col-6.mb-lg-0.mb-4(data-aos="fade-right")
          img(src='@/assets/curso/tema2/img61.png' alt="Imagen decorativa")
        .col-lg-9(data-aos="fade-left")
          SlyderF.mb-5(columnas="col-lg-6 col-xl-4")
            .tarjeta.tarjeta--blanca.bs.p-4
              .row.justify-content-center.mb-4
                .col-6
                  img(src='@/assets/curso/tema2/img62.svg' alt="Imagen decorativa")
              .h4.text-center #[mark.BG02.px-1 Programa de elementos de proteccin personal (EPP)]
              p.mb-0 Garantiza la seleccin, dotacin, uso adecuado y mantenimiento de los elementos de proteccin requeridos para cada actividad laboral.
            .tarjeta.tarjeta--blanca.bs.p-4
              .row.justify-content-center.mb-4
                .col-6
                  img(src='@/assets/curso/tema2/img63.svg' alt="Imagen decorativa")
              .h4.text-center #[mark.BG02.px-1 Programa de fomento de estilos de vida saludable]
              p.mb-0 Promueve hbitos que favorecen la salud fsica y mental de los trabajadores, como alimentacin balanceada, actividad fsica y manejo del estrs.
            .tarjeta.tarjeta--blanca.bs.p-4
              .row.justify-content-center.mb-4
                .col-6
                  img(src='@/assets/curso/tema2/img64.svg' alt="Imagen decorativa")
              .h4.text-center #[mark.BG02.px-1 Programa de intervencin del riesgo psicosocial]
              p.mb-0 Identifica, evala y mitiga factores que afectan el bienestar emocional y psicolgico de los trabajadores, tales como la carga laboral, el acoso o el clima organizacional negativo.
            .tarjeta.tarjeta--blanca.bs.p-4
              .row.justify-content-center.mb-4
                .col-6
                  img(src='@/assets/curso/tema2/img65.svg' alt="Imagen decorativa")
              .h4.text-center #[mark.BG02.px-1 Programas de vigilancia epidemiolgica]
              p.mb-0 Monitorean de forma permanente las condiciones de salud de los trabajadores para detectar, controlar y prevenir enfermedades relacionadas con el trabajo.
            .tarjeta.tarjeta--blanca.bs.p-4
              .row.justify-content-center.mb-4
                .col-6
                  img(src='@/assets/curso/tema2/img66.svg' alt="Imagen decorativa")
              .h4.text-center #[mark.BG02.px-1 Programa de mantenimiento preventivo y correctivo]
              p.mb-0 Asegura que las instalaciones, herramientas y equipos estn en condiciones ptimas para su uso seguro, evitando fallas que puedan generar riesgos.
            .tarjeta.tarjeta--blanca.bs.p-4
              .row.justify-content-center.mb-4
                .col-6
                  img(src='@/assets/curso/tema2/img67.svg' alt="Imagen decorativa")
              .h4.text-center #[mark.BG02.px-1 Programa de prevencin, preparacin y respuesta ante emergencias]
              p.mb-0 Establece los lineamientos y procedimientos para actuar ante situaciones de emergencia como incendios, terremotos, fugas qumicas, entre otros.
            .tarjeta.tarjeta--blanca.bs.p-4
              .row.justify-content-center.mb-4
                .col-6
                  img(src='@/assets/curso/tema2/img68.svg' alt="Imagen decorativa")
              .h4.text-center #[mark.BG02.px-1 Programa de induccin, capacitacin y entrenamiento]
              p.mb-0 Facilita el aprendizaje continuo en materia de seguridad y salud, fortaleciendo competencias y promoviendo la apropiacin de prcticas seguras.
            .tarjeta.tarjeta--blanca.bs.p-4
              .row.justify-content-center.mb-4
                .col-6
                  img(src='@/assets/curso/tema2/img69.svg' alt="Imagen decorativa")
              .h4.text-center #[mark.BG02.px-1 Programa de saneamiento bsico ambiental]
              p.mb-0 Controla aspectos como la limpieza, desinfeccin, manejo de residuos y condiciones higinicas generales en el entorno laboral.
            .tarjeta.tarjeta--blanca.bs.p-4
              .row.justify-content-center.mb-4
                .col-6
                  img(src='@/assets/curso/tema2/img70.svg' alt="Imagen decorativa")
              .h4.text-center #[mark.BG02.px-1 Programa de auditora]
              p.mb-0 Evala de manera peridica el cumplimiento y efectividad del SG-SST, proponiendo acciones correctivas y de mejora continua.

    p.mb-5 Otros programas complementarios segn el contexto laboral:

    .row.justify-content-center.mb-5
      .col-lg-10
        .tarjeta.BG06.p-4
          ul.lista-ul--color
            li(style="margin-bottom: 2px" data-aos="fade-right")
              i.fa-solid.fa-helmet-safety.TC2
              p.mb-0 #[b Programa de uso seguro de herramientas de mano:] ensea prcticas seguras para la manipulacin y mantenimiento de herramientas manuales, reduciendo riesgos de lesiones.
            li(style="margin-bottom: 2px" data-aos="fade-right")
              i.fa-solid.fa-helmet-safety.TC2
              p.mb-0 #[b Programa para la prevencin del riesgo elctrico:] establece medidas preventivas para evitar accidentes por contacto elctrico, cortocircuitos y sobrecargas.
            li(style="margin-bottom: 2px" data-aos="fade-right")
              i.fa-solid.fa-helmet-safety.TC2
              p.mb-0 #[b Programa para la prevencin de patologas comunes:] busca reducir enfermedades prevalentes como trastornos osteomusculares o enfermedades respiratorias, asociadas al tipo de labor.
            li(style="margin-bottom: 2px" data-aos="fade-right")
              i.fa-solid.fa-helmet-safety.TC2
              p.mb-0 #[b Programa de rehabilitacin de la salud de los trabajadores:] facilita la recuperacin e inclusin laboral de trabajadores que han sufrido accidentes o enfermedades de origen laboral.
            li(style="margin-bottom: 2px" data-aos="fade-right")
              i.fa-solid.fa-helmet-safety.TC2
              p.mb-0 #[b Programa de prevencin y proteccin contra cadas:] define medidas para realizar trabajos en alturas de manera segura, reduciendo el riesgo de cadas graves.
            li(style="margin-bottom: 2px" data-aos="fade-right")
              i.fa-solid.fa-helmet-safety.TC2
              p.mb-0 #[b Programa de capacitacin para trabajo seguro en alturas:] forma al personal que ejecuta labores a ms de 1.5 metros de altura, cumpliendo con los requisitos legales y tcnicos establecidos.

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-1.col-2.px-0.mb-lg-0.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/temas/img01.svg' alt="Imagen decorativa")
      .col-lg-11(data-aos="fade-left")
        p.mb-0 Cada uno de estos programas debe estar documentado, contar con responsables asignados, cronogramas definidos, recursos adecuados y mecanismos de seguimiento para garantizar su efectividad y cumplimiento de la normatividad vigente.

    separador
    #t_2_9.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 2.9 Reglamentos de higiene, seguridad y salud en el trabajo

    .row.justify-content-center.mb-5
      .col-lg-1.col-2.mb-lg-0.mb-4(data-aos="fade-right")
        img(src='@/assets/curso/tema2/img71.svg' alt="Imagen decorativa")
      .col-lg-11(data-aos="fade-left")
        p.mb-0 Los reglamentos de higiene, seguridad y salud en el trabajo son documentos normativos internos que definen las condiciones, normas y procedimientos que deben seguir tanto empleadores como trabajadores para garantizar un ambiente laboral seguro, saludable y conforme con la legislacin vigente. Estos reglamentos son de obligatorio conocimiento y cumplimiento por parte de todos los integrantes de la organizacin.

    TabsC.color-acento-botones.mb-5(data-aos="zoom-out")
      .BGIMG02.p-4(titulo="a)	Reglamento interno de trabajo")
        .row.justify-content-center
          .col-lg-5.col-10.mb-lg-0.mb-4
            img(src='@/assets/curso/tema2/img72.png' alt="Imagen decorativa")
          .col-lg-7
            p.mb-0 Es un documento que contiene el conjunto de normas que regulan las relaciones laborales dentro de la empresa. Establece las condiciones a las que deben sujetarse empleador y trabajadores en la prestacin del servicio, incluyendo derechos, deberes, sanciones y procedimientos disciplinarios. Este reglamento se elabora de acuerdo con lo dispuesto en el Cdigo Sustantivo del Trabajo y debe ser aprobado por el Ministerio del Trabajo.
      .BGIMG02.p-4(titulo="b)	Reglamento de Higiene y Seguridad Industrial")
        .row.justify-content-center
          .col-lg
            p De acuerdo con el artculo 349 del Cdigo Sustantivo del Trabajo, todo empleador que tenga a su servicio 10 o ms trabajadores permanentes est obligado a elaborar un reglamento de higiene y seguridad industrial. Este documento tiene como finalidad prevenir accidentes y enfermedades laborales, estableciendo normas claras sobre el comportamiento esperado en materia de seguridad. El reglamento debe contemplar:
            ul.lista-ul--color
              li(style="margin-bottom: 2px")
                i.fa-solid.fa-helmet-safety.TC2
                | Los peligros asociados a la actividad econmica y los procesos de la empresa.
              li(style="margin-bottom: 2px")
                i.fa-solid.fa-helmet-safety.TC2
                | Las obligaciones del empleador y de los trabajadores en cuanto a la prevencin de riesgos laborales.
              li(style="margin-bottom: 2px")
                i.fa-solid.fa-helmet-safety.TC2
                | La adopcin de normas tcnicas y legales vigentes en materia de salud ocupacional.
              li(style="margin-bottom: 2px")
                i.fa-solid.fa-helmet-safety.TC2
                | La implementacin de programas de medicina preventiva, higiene industrial y seguridad laboral.
              li(style="margin-bottom: 2px")
                i.fa-solid.fa-helmet-safety.TC2
                | Las acciones necesarias para el cumplimiento del Sistema de Gestin de Seguridad y Salud en el Trabajo (SG-SST).

    p Ambos reglamentos deben ser revisados y actualizados de forma peridica, especialmente cuando se presenten cambios en los procesos productivos, en la normativa legal o en los riesgos identificados en el lugar de trabajo. Adems, es responsabilidad del empleador garantizar su adecuada divulgacin entre los trabajadores, mediante inducciones, capacitaciones o medios fsicos y digitales accesibles.
    p El cumplimiento de estos reglamentos fortalece la cultura de autocuidado, promueve ambientes de trabajo ms seguros y saludables, y contribuye al cumplimiento de la normativa laboral colombiana.

    separador
    #t_2_10.titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 2.10 Polticas de seguridad y salud en el trabajo
    
    .bloque-texto-g.bloque-texto-g--inverso.color-secundario.p-3.p-sm-4.p-md-5.mb-5
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/curso/tema2/img73.jpg')})`}" data-aos="fade-left"
      )
      .bloque-texto-g__texto.p-4(data-aos="fade-right")
        p.mb-0 Las polticas de seguridad y salud en el trabajo son un conjunto de principios y directrices adoptadas por las empresas para promover y proteger la salud y bienestar de los trabajadores, asegurando un ambiente laboral seguro y saludable. Estas polticas deben ser claramente definidas, comunicadas a todos los niveles organizacionales y reflejar el compromiso de la empresa con la prevencin de accidentes, enfermedades laborales y la mejora continua en los procesos de seguridad y salud en el trabajo.
    
    p.mb-5 Los objetivos de la poltica de seguridad y salud en el trabajo contienen:

    .row.justify-content-center.mb-5
      .col-lg-3.col-sm-6.col-10.mb-lg-0.mb-4(data-aos="fade-right")
        .tarjeta.color-primario.h-100
          figure
            img(src='@/assets/curso/tema2/img74.png' alt="Imagen decorativa")
          .p-4
            p.mb-0 #[b Prevencin de accidentes y enfermedades laborales:] la poltica debe enfocarse en identificar, evaluar y controlar los riesgos que puedan causar dao a la salud de los trabajadores, implementando medidas preventivas adecuadas.
      .col-lg-3.col-sm-6.col-10.mb-lg-0.mb-4(data-aos="flip-right")
        .tarjeta.color-secundario.h-100
          figure
            img(src='@/assets/curso/tema2/img75.png' alt="Imagen decorativa")
          .p-4
            p.mb-0 #[b Cumplimiento de la legislacin vigente:] es fundamental que la poltica cumpla con las normativas nacionales e internacionales relacionadas con la seguridad y salud en el trabajo, como lo estipula la Ley 1562 de 2012 y los Decretos 1072 de 2015 y 1443 de 2014, que regulan la gestin del SG-SST.
      .col-lg-3.col-sm-6.col-10.mb-lg-0.mb-4(data-aos="flip-right")
        .tarjeta.color-primario.h-100
          figure
            img(src='@/assets/curso/tema2/img76.png' alt="Imagen decorativa")
          .p-4
            p.mb-0 #[b Promocin de la salud laboral:] adems de prevenir accidentes, las polticas deben fomentar hbitos saludables entre los trabajadores, promoviendo el bienestar fsico y mental dentro del ambiente laboral.
      .col-lg-3.col-sm-6.col-10.mb-lg-0.mb-4(data-aos="fade-right")
        .tarjeta.color-secundario.h-100
          figure
            img(src='@/assets/curso/tema2/img77.png' alt="Imagen decorativa")
          .p-4
            p.mb-0 #[b Participacin activa de los trabajadores:] la poltica debe asegurar la participacin activa de los trabajadores en la implementacin y evaluacin del SG-SST, ya que ellos son los que tienen el conocimiento directo sobre los riesgos presentes en sus actividades diarias.

    p.mb-5 Es importante identificar los componentes clave de una poltica de seguridad y salud en el trabajo:

    .BGM09.px-md-5.px-4.mb-4
      .row.justify-content-center.align-items-center.mb-5
        .col-lg-7.order-lg-1.order-2(data-aos="fade-right")
          LineaTiempoD.color-acento-botones
            p(numero="A" titulo="Compromiso de la alta direccin") La direccin de la empresa debe mostrar su total compromiso con la implementacin de las polticas de seguridad y salud, asignando recursos necesarios y liderando el cambio cultural dentro de la organizacin.
            p(numero="B" titulo="Identificacin y evaluacin de riesgos") La poltica debe establecer un proceso continuo de identificacin, evaluacin y control de los peligros presentes en el entorno laboral, asegurando que todas las reas de trabajo estn evaluadas y se tomen medidas adecuadas para mitigar los riesgos.
            p(numero="C" titulo="Capacitacin y entrenamiento") La formacin continua en temas de seguridad y salud es crucial. Los trabajadores deben recibir capacitacin peridica sobre los riesgos a los que estn expuestos y sobre las mejores prcticas para prevenir accidentes y enfermedades.
            p(numero="D" titulo="Manejo de emergencias") La poltica debe incluir procedimientos claros para actuar en caso de emergencias laborales, tales como incendios, derrames qumicos, accidentes graves, entre otros.
            p(numero="E" titulo="Monitoreo y mejora continua") Es esencial que la poltica de seguridad y salud en el trabajo incluya mecanismos de seguimiento y evaluacin para medir su efectividad. Esto puede implicar auditoras peridicas, informes de accidentes, anlisis de incidentes y la revisin de indicadores de salud laboral.
            p(numero="F" titulo="Bienestar psicolgico") Considerando el impacto de factores psicosociales en la salud laboral, la poltica debe abordar aspectos como el estrs laboral, el acoso laboral, la carga emocional y la gestin del bienestar psicolgico de los trabajadores.
        .col-lg-3.col-8.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="fade-left")
          img(src='@/assets/curso/tema2/img78.jpg' alt="Imagen decorativa")

    p.mb-5 Los beneficios de implementar una poltica de seguridad y salud en el trabajo son:

    .row.justify-content-center.mb-5
      .col-lg-3.col-sm-6.col-10.mb-4(data-aos="fade-right")
        .tarjeta.BGIMG04.h-100.p-4
          .row.mb-4
            .col-lg-3.col-5
              img(src='@/assets/curso/tema2/img79.svg' alt="Imagen decorativa")
          p.mb-0 #[b Reduccin de accidentes y enfermedades:] una correcta implementacin de la poltica contribuye a la reduccin significativa de accidentes de trabajo y enfermedades profesionales.
      .col-lg-3.col-sm-6.col-10.mb-4(data-aos="flip-right")
        .tarjeta.BGIMG04.h-100.p-4
          .row.mb-4
            .col-lg-3.col-5
              img(src='@/assets/curso/tema2/img80.svg' alt="Imagen decorativa")
          p.mb-0 #[b Mejora en la productividad:] un entorno de trabajo seguro y saludable mejora el rendimiento laboral, reduciendo el ausentismo y la rotacin de personal.
      .col-lg-3.col-sm-6.col-10.mb-4(data-aos="flip-left")
        .tarjeta.BGIMG04.h-100.p-4
          .row.mb-4
            .col-lg-3.col-5
              img(src='@/assets/curso/tema2/img81.svg' alt="Imagen decorativa")
          p.mb-0 #[b Cumplimiento normativo:] cumplir con las normativas nacionales e internacionales evita sanciones y mejora la reputacin de la empresa.
      .col-lg-3.col-sm-6.col-10.mb-4(data-aos="fade-left")
        .tarjeta.BGIMG04.h-100.p-4
          .row.mb-4
            .col-lg-3.col-5
              img(src='@/assets/curso/tema2/img82.svg' alt="Imagen decorativa")
          p.mb-0 #[b Bienestar general de los trabajadores:] los trabajadores que se sienten seguros y protegidos son ms felices, lo que contribuye a un ambiente laboral positivo y una mayor satisfaccin en el trabajo.

